
import Search from '@/components/pages/search'
import Banner from '@/components/pages/banner'
import Divider from 'vux/src/components/divider/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
Divider,
FlexboxItem,
Flexbox,
Panel,
Group,
Search,
Banner
},
data() {
return {
newest: '',
nlist: '',
popular: '',
plist: '',
recommend: '',
rlist: ''
}
},
created: function() {
this.getNewest();
this.getNlist();
this.getPopular();
this.getPlist();
this.getRecommend();
this.getRlist();
},
methods: {
download: function() {
alert('开发中.. 敬请期待!')
},
getNewest: function() {
this.$http.jsonp("http://exp.pgzs.com/soft.ashx?act=257&pi=1&ps=5").then(response => {
this.newest = response.body.Result.items
}, error => {
console.log(error);
});
},
getNlist: function() {
this.$http.jsonp("http://exp.pgzs.com/soft.ashx?act=257&pi=5&ps=10").then(response => {
this.nlist = response.body.Result.items
}, error => {
console.log(error);
});
},
getPopular: function() {
this.$http.jsonp("http://exp.pgzs.com/soft.ashx?act=242&pi=1&ps=5").then(response => {
this.popular = response.body.Result.items
}, error => {
console.log(error);
});
},
getPlist: function() {
this.$http.jsonp("http://exp.pgzs.com/soft.ashx?act=242&pi=5&ps=10").then(response => {
this.plist = response.body.Result.items
}, error => {
console.log(error);
});
},
getRecommend: function() {
this.$http.jsonp("http://exp.pgzs.com/soft.ashx?act=202&pi=0&ps=5").then(response => {
this.recommend = response.body.Result.items
}, error => {
console.log(error);
});
},
getRlist: function() {
this.$http.jsonp("http://exp.pgzs.com/soft.ashx?act=202&pi=5&ps=10").then(response => {
this.rlist = response.body.Result.items
}, error => {
console.log(error);
});
}
}
}



