
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

const baseList = [{
url: 'javascript:;',
img: "static/images/11ea70b248424710.jpg",
title: '付费游戏免费玩第1期'
},{
url: 'javascript:;',
img: 'static/images/22fc9ad744b4f15af9c.jpg',
title: '周末游品位第1期'
},{
url: 'javascript:;',
img: 'static/images/33dcf368373e7b48c29ddd.jpg',
title: '生存游戏大合集'
}]

const urlList = baseList.map((item, index) => ({
url: item.url,
img: item.img,
title: `${item.title}`
}))

export default {
components: {
Swiper,
SwiperItem,
GroupTitle,
XButton,
Divider
},
ready () {
},
methods: {
banner_onIndexChange (index) {
this.banner_index = index
}
},
data () {
return {
banner_list: urlList
}
}
}



